.header {
    width: 100%;
    height: 100%;
    background-image: url("/public/imgs/index-image.jpg");
    background-position: center;
    background-size: 120%;
    background-repeat: no-repeat;
    display: flex;
    justify-content: center;
  }
  .body_header {
    margin-top: 20px;
    width: 55%;
    padding: 20px;
  }
  .body_header_nav {
    display: flex;
    flex-direction: row;
  }
  .body_header_nav_logo {
    width: 100%;
  }
  .body_header_nav_list {
    /* Helvetica 25 UltraLight Regular */
    font-family: Helvetica;
    width: 100%;
    display: flex;
    align-items: flex-end;
    justify-content: flex-end;
    padding-right: 25px;
    cursor: pointer;
    margin-bottom: 10px;
  }
  .body_header_nav_list ul {
    display: flex;
    flex-direction: row;
    list-style: none;
    gap: 10px;
    color: rgb(230, 230, 230);
  }
  
  /* titulo header */
  
  .body_header_titulo {
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 70%;
  }
  
  .body_header_titulo p {
    font-family: "Roboto Flex", sans-serif;
    font-weight: 500;
    color: rgb(230, 230, 230);
    font-size: 105px;
  }
  
  .body_header_titulo label {
    font-family: "Roboto Flex", sans-serif;
    font-weight: 100;
    color: rgb(230, 230, 230);
    font-size: 40px;
  }
  .body_header_nav_list_mobile {
    display: none;
    background-color: #29abe2;
    color: white;
    width: 45%;
    height: 20%;
    position: absolute;
  }
  .body_header_nav_list_mobile ul {
    list-style: none;
    font-family: "Roboto Flex", sans-serif;
    font-size: 30px;
  }
  
  .body_header_button_mobile {
    display: none;
  }
  
  @media screen and (max-width: 1200px) {
    .header {
      background-position: center;
    }
  }
  
  @media screen and (max-width: 450px) {
    /* FALTA AJUSTAR O MENU MOBILE */
    .body_header_button_mobile {
      width: 450px;
      display: flex;
      margin-right: 200px;
      background-color: rgba(240, 248, 255, 0);
      border: none;
      background-image: url("/public/icons/hamburguer.svg");
      background-repeat: no-repeat;
      cursor: pointer;
    }
    .body_header_nav_list {
      display: none;
    }
    .header {
      background-image: url("/public/imgs/index-image-mobile.jpg");
      background-size: 100%;
      background-position: center;
    }
    .body_header {
      margin-top: 10px;
    }
    .body_header_titulo p {
      margin-top: 0px;
      text-align: center;
      font-size: 100px;
    }
    .body_header_titulo label {
      text-align: center;
    }
  }