.lista {
    width: 100%;
    height: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 50px;
  }
  .lista_titulo {
    color: #29abe2;
    font-family: "Roboto Flex", sans-serif;
    font-size: 39px;
    margin-bottom: 50px;
  }
  .body_table {
    width: 80%;
    height: 400px;
    overflow: auto;
    color: #012d51;
  }
  
  .lista_tabela {
    text-align: center;
    width: 100%;
    color: rgb(145, 145, 145);
    box-shadow: inset rgb(255, 255, 255) 1px 1px 1px 12px;
    background-color: #29abe2;
    font-family: 'Roboto Flex', sans-serif;
  }
  .lista_tabela tr {
   
    background-color: rgb(255, 255, 255);
  }
  .lista_tabela td {
    padding-left: 10px;
    padding-right: 10px;
    padding-top: 20px;
    color: #012d51;
    padding-bottom: 20px;
  }
  .lista_tabela thead tr th {
    font-family: Arial, Helvetica, sans-serif;
    color: #012d51;
      padding-top: 20px;
      padding-bottom: 20px;
  }