.cadastro {
    width: 100%;
    height: auto;
    background-color: #29abe2;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .cadastro_body_titulo {
    margin-top: 20px;
    font-family: "Roboto Flex", sans-serif;
    font-weight: 500;
    color: rgb(255, 255, 255);
    font-size: 55px;
    text-align: center;
    margin-bottom: 60px;
  }
  .cadastro_body {
    width: 50%;
    height: 100%;
  }
  .cadastro_body_form {
    width: 100%;
    height: 60%;
  }
  
  .cadastro_body_form_btn {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
  }
  
  .cadastro_body_form_button {
    font-size: 34px;
    padding-bottom: 7px;
    padding-left: 20px;
    padding-right: 20px;
    color: #29abe2;
    background-color: #012d51;
    font-family: "Roboto Flex", sans-serif;
    align-items: center;
    cursor: pointer;
  }
  
  /* styles input */
  .cadastro_body_form_input {
    display: flex;
    flex-direction: column;
  }
  .cadastro_body_form_input label {
    color: rgb(255, 255, 255);
    font-family: Helvetica;
    font-size: 13px;
  }
  .cadastro_body_form_input input {
    margin-bottom: 20px;
    font-family: Helvetica;
    background-color: rgba(255, 255, 255, 0);
    border: none;
    border-bottom: 2px solid rgb(214, 214, 214);
    padding: 8px;
    color: white;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: 500;
  }
  .cadastro_body_form_input input:focus {
    box-shadow: 0 0 0 0;
    outline: 0;
  }
  
  @media screen and (max-width: 700px) {
      .cadastro_body_titulo{
        margin-top: 20px;
        font-size: 38px;
        margin-bottom: 50px;
      }
      .cadastro_body{
        width: 75%;
      }
      .cadastro_body_form_input label {
        font-size: 10px;
      }
      .cadastro_body_form_input input {
        font-size: 10px;
      }
  }