.footer {
    width: 100%;
    height: 100%;
    align-items: center;
    display: flex;
    background-image: url("/public/imgs/rodape-desktop.jpg");
    background-position: center;
    background-repeat: no-repeat;
    background-size: 160%;
    margin-bottom: 0;
  }
  .footer_body {
    gap: 10px;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    color: rgb(212, 212, 212);
    font-family: Helvetica;
    font-size: 13px;
  }
  
  @media screen and (min-width: 1650px) {
    .footer {
      background-size: 300%;
    }
  }
  
  @media screen and (max-width: 750px) {
    .footer {
      background-image: url("/public/imgs/rodape-mobile.jpg");
    }
    .footer_body {
      font-size: 8px;
    }
  }